import React from "react";
import 'bulma/css/bulma.min.css';
import { useRoutes } from './routes/router.js';
import { BrowserRouter } from "react-router-dom";
import { Container } from "react-bulma-components";

function App() {
  const routes = useRoutes();
  return (
    <>
    <Container>
      <BrowserRouter>
        {routes}
      </BrowserRouter>
    </Container>
    </>
  );
}

export default App;
